@import url('https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;500;600;700;800&display=swap')
$font: 'Baloo Tamma 2', cursive 
$clr1: #45454d
$clr2: #fc7fb2
$clr3: #ffd5d5
$clr4: #fff1e9
$clr5: #a13355
$shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12)

::-webkit-scrollbar
  width: 12px

/* Track */
::-webkit-scrollbar-track
  background: $clr4
  border-radius: 8px

/* Handle */
::-webkit-scrollbar-thumb
  background: $clr1
  border-radius: 8px

/* Handle on hover */
::-webkit-scrollbar-thumb:hover
  background: $clr5

*
  font-family: $font
  box-sizing: border-box
  color: $clr1

%d-flex-r
  display: flex
  flex-direction: row
  justify-content: center
  align-items: center

%d-flex-c
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
